        "id": 2204678213,
        "title": "Cobham Coat Wallet",
        "handle": "cobham-coat-wallet",
        "body_html": "<p>Bremonts Cobham Coat Wallet includes eight credit card compartments, two full length pockets for ample note storage and two large sections to stow additional documents. The wallet features blind Bremont logo and propeller embossing and is crafted from deluxe calf and English bridle leather, guaranteeing excellent quality.</p>\n<p>Made in England, the Cobham Coat Wallet approximately measures a width of 10.7cm by 18.3cm height, sizes may vary marginally.</p>\n<p>See how this product is made in our film below.</p>\n<iframe width=\"500\" height=\"300\" src=\"https://www.youtube.com/embed/3qIV8zRGJ0s?rel=0&amp;showinfo=0\" frameborder=\"0\" allowfullscreen=\"\"></iframe>",
