        "title": "NATO Strap - Khaki",
        "handle": "nato-strap-khaki",
        "body_html": "<p>Khaki NATO strap with Velcro fastening, featuring a hand-stitched Bremont logo.</p>\n<p>Handmade in England and designed for ultimate functionality, the Bremont NATO strap features a simple fail-safe method to keep the watch secure in the event a spring bar breaks or detaches.</p>\n<p>The strap is made from durable nylon and features leather keepers with a <span>stainless steel</span> loop.</p>\n<p><a href=\"http://www.bremont.com/images/files/Bremont-Strap-Sizing-Guide.pdf\" target=\"_blank\" rel=\"noopener noreferrer\">View our Strap Sizing Guide here</a></p>",
