        "id": 8504510729,
        "title": "Hip Flask",
        "handle": "hip-flask",
        "body_html": "<p class=\"p1\">Bremonts 4oz stainless steel Hip Flask is hand covered using fine English leather to promise a luxurious feel. The hunter green, kidney shaped flask features signature embossing and a hinged screw top, making it practical for all outdoor pursuits and adventures. Bremonts Hip Flask measures a width of 9.8cm by 9.4cm height, sizes may vary marginally. Please note that our flask is suitable for cold drinks only.</p>",
